"use server";

import { revalidatePath } from "next/cache";
import { redirect } from "next/navigation";

//REGISTER

export async function createUser(
  email: string,
  password: string,
  role: string = "admin"
) {
  // Await the result of createServerClientInstance
  const supabase = await createAdminClient();

  // Step 1: Create a user without logging in
  const { data, error } = await supabase.auth.admin.createUser({
    email,
    password,
    email_confirm: true,
    user_metadata: { role: "admin" }, // or "editor"
  });

  if (error) {
    console.error("Error creating user:", error); // Log the full error object
    throw new Error("Failed to create user: " + error.message);
  }

  const userId = data.user?.id;

  if (!userId) {
    throw new Error("Failed to retrieve user ID after creating user.");
  }

  // Step 2: Create a profile in the `profiles` table
  const { error: profileError } = await supabase
    .from("profiles")
    .insert([{ id: userId, role: "admin" }]);

  if (profileError) {
    throw new Error("Failed to create profile: " + profileError.message);
  }

  console.log(`Admin user ${email} created successfully.`);
}

//LOGOUT

export async function signOut() {
  const supabase = await createServerClientInstance();

  await supabase.auth.signOut();

  revalidatePath("/", "layout");
  redirect("/login");
}

// ADMIN CHECK

export async function isAdmin() {
  const supabase = await createServerClientInstance();

  const {
    data: { user },
    error: authError,
  } = await supabase.auth.getUser();

  if (authError) {
    throw new Error("Failed to authenticate user: " + authError.message);
  }

  if (!user) {
    return false;
  }

  const { data: profile, error: profileError } = await supabase
    .from("profiles")
    .select("role")
    .eq("id", user.id)
    .single();

  if (profileError) {
    throw new Error("Failed to fetch user role: " + profileError.message);
  }

  return profile.role === "admin";
}

// ADMIN GET ALL USERS

export async function getAllUsers() {
  const supabase = await createServerClientInstance();

  const admin = await isAdmin();

  if (!admin) {
    throw new Error("Unauthorized: Only admins can access this resource");
  }

  // Hent alle brugere
  const {
    data: { users },
    error: fetchError,
  } = await supabase.auth.admin.listUsers();

  if (fetchError) {
    throw new Error("Failed to fetch users: " + fetchError.message);
  }

  return users || [];
}

export async function getUserRole() {
  const supabase = await createServerClientInstance();

  const {
    data: { user },
    error: authError,
  } = await supabase.auth.getUser();

  if (authError) {
    throw new Error("Failed to authenticate user: " + authError.message);
  }

  if (!user) {
    throw new Error("Unauthorized: No user logged in");
  }

  const { data: profile, error: profileError } = await supabase
    .from("profiles")
    .select("role")
    .eq("id", user.id)
    .single();

  if (profileError) {
    throw new Error("Failed to fetch user role: " + profileError.message);
  }

  const role = profile.role;
  const isAdmin = role === "admin";

  return { role, isAdmin };
}

export async function deleteUser(userId: string, requesterId: string) {
  const supabase = await createServerClientInstance();

  // Check if the requester is an admin
  const { data: profile, error: profileError } = await supabase
    .from("profiles")
    .select("role")
    .eq("id", requesterId)
    .single();

  if (profileError) {
    throw new Error(
      "Failed to retrieve requester profile: " + profileError.message
    );
  }

  if (profile.role !== "admin") {
    throw new Error("Only admins can delete users.");
  }

  // Proceed with user deletion
  const { error } = await supabase.auth.admin.deleteUser(userId);

  if (error) {
    console.error("Error deleting user:", error.message);
  } else {
    console.log("User deleted successfully");
  }
}
